import React, { Component } from "react";
import { observer, inject } from "mobx-react";
import { Card, Form, Input, Button, message } from "antd";
import UploadImage from "~/components/UploadImage";

const statusName = status => {
  switch (status) {
    case "WAIT_AUDIT":
      return "";
    case "AUDITING":
      return "...";
    default:
      return "";
  }
};

@inject("userStore")
@Form.create()
@observer
class Organization extends Component {
  componentDidMount() {
    this.props.userStore.getOrganization();
  }
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.userStore.submitOrganizationInfo(values);
        // const { user } = this.props.userStore;
        // this.props.userStore.updateUser(values, user.id);
      }
    });
  };
  renderInfo = organization => {
    const { status, name } = organization;

    if (status === "WAIT_AUDIT" || status === "AUDITING") {
      return statusName(status);
    }

    if (status === "FAIL") {
      message.error("");
    }

    return (
      <Form>
        <Form.Item label="">{name}</Form.Item>
      </Form>
    );
  };
  render() {
    const { organization, user } = this.props.userStore;
    const { getFieldDecorator } = this.props.form;
    return (
      <Card size="small" title={organization.id ? "" : ""}>
        {organization.id ? (
          this.renderInfo(organization)
        ) : (
          <Form onSubmit={this.handleSubmit}>
            <Form.Item label="" required>
              {getFieldDecorator("name", {
                rules: [
                  {
                    required: true,
                    message: ""
                  }
                ]
              })(<Input style={{ width: 300 }} placeholder="" />)}
            </Form.Item>
            <Form.Item label={""} required>
              {getFieldDecorator("credential", {
                rules: [
                  {
                    required: true,
                    message: ""
                  }
                ],
                initialValue: user.originationCertificatePicture
              })(<UploadImage />)}
            </Form.Item>
            <Button htmlType="submit" type="primary">
              
            </Button>
          </Form>
        )}
      </Card>
    );
  }
}

export default observer(Organization);
