import React from 'react';
import { Input, Select } from 'antd';

const InputGroup = Input.Group;
const Option = Select.Option;

const getSearchUrl = type => `http://chou.foodmate.net/xz/${type}/index.html`;
const types = {
    category: 'category', // 
    additive: 'additive', // 
}
const SearchInput = Input.Search;

class Search extends React.PureComponent {
    state = {
        type: types.category,
    }
    handleSearch = (value) => {
        const { type } = this.state;
        window.open(`${getSearchUrl(type)}?q=${value}`);
    }
    handleTypeChange = (value) => {
        this.setState({ type: value });
    }
    render() {
        const { className } = this.props;
        return (
            <div style={{ float: 'right', height: 75 }}>
                <InputGroup className={className} compact>
                    <Select defaultValue={types.category} onChange={this.handleTypeChange}>
                        <Option value={types.category}></Option>
                        <Option value={types.additive}></Option>
                    </Select>
                    <SearchInput className={'home-search-input'} placeholder='' onSearch={this.handleSearch} />
                </InputGroup>
            </div>
        );
    }
}

export default Search;
