import { observable, action } from 'mobx';

class Table {
  // 
  @observable data = [];
  // 
  @observable loading = false;
  // 
  @observable conditions = {
    pagination: {
      current: 1,
      pageSize: 10
    },
    filters: {},
    sorter: {}
  };

  @action
  setData(data = []) {
    this.data = data;
  }

  @action
  setLoading(bool = false) {
    this.loading = bool;
  }

  @action
  setConditions(conditions = {}) {
    this.conditions = conditions;
  }
}

export default Table
