import React from "react";
import { inject, observer } from "mobx-react";
import { Form } from "antd";
import moment from "moment";
import styled from "styled-components";

const Container = styled.h1`
  padding: 40px;
`;

const Title = styled.h1`
  color: #62a4f1;
  font-size: 20px;
`;

const Content = styled.div`
  color: #000;
  font-size: 14px;
`;

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 5 }
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 12 }
  }
};

@inject("reportStore")
@observer
class ModalContent extends React.PureComponent {
  render() {
    const { detail } = this.props.reportStore;
    
    return (
      <Container>
        <Title>:</Title>
        <Content>
          <Form {...formItemLayout}>
            <Form.Item label="">{detail.name}</Form.Item>
            <Form.Item label="">{detail.examiningResult}</Form.Item>
            <Form.Item label="">
              {moment(detail.createdData).format("YYYY-MM-DD HH:mm")}
            </Form.Item>
            {/* <Form.Item label="">
              <img src={`${detail.path}`} alt="" />
            </Form.Item> */}
            <Form.Item label="">{detail.evaluation || ""}</Form.Item>
          </Form>
        </Content>
      </Container>
    );
  }
}

export default ModalContent;
