import React from 'react';
import { Dropdown, Menu } from 'antd';

const data = [
  {
    name: '',
    children: [{ name: '', url: 'http://chou.foodmate.net/xz/' }]
  },
  {
    name: '',
    children: [
      {name: '', url: 'http://mpa.zj.gov.cn'},
      {name: '', url: 'http://da.jiangsu.gov.cn/'},
      {name: '', url: 'http://www.shfda.gov.cn/'},
      {name: '', url: 'http://mpa.shandong.gov.cn/'},
      {name: '', url: 'scjgj.beijing.gov.cn'}
    ]
  },
  {
    name: '',
    children: [
      {name: '', url: 'http://scjg.hangzhou.gov.cn/'},
      {name: '', url: 'http://scjgj.ningbo.gov.cn/'},
      {name: '', url: 'http://www.hufs.gov.cn/'},
    ]
  },
  {
    name: '',
    children: [
      {name: '', url: '/'},
      {name: '', url: '/'},
      {name: '', url: '/'},
    ]
  }
];

const HomeLink = () => {
  const renderMenu = children => {
    if (!children || children.length === 0) return <div />;
    return (
      <Menu>
        {children.map(item => (
          <Menu.Item key={item.url}>
            <a href={item.url} target="_blank" rel="noopener noreferrer">
              {item.name}
            </a>
          </Menu.Item>
        ))}
      </Menu>
    );
  };
  return (
    <div className="home-link-list">
      {data.map((item, index) => {
        return (
          <Dropdown overlayClassName="home-link-dropdown" key={item.name + index} trigger={['hover']} overlay={renderMenu(item.children)}>
            <div className="home-link-item">{item.name}</div>
          </Dropdown>
        );
      })}
    </div>
  );
};

export default HomeLink;
